body {
  font-family: "Josefin Sans", cursive;
  background-color: #ece7e7;
}
.navbar {
  overflow: hidden;
  background-color: rgb(248, 177, 11);
}

.navbar a {
  float: left;
  font-size: 25px;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  z-index: 300;
}

.navbar a:hover {
  background-color: rgb(218, 167, 0);
}

.rightNav {
  float: right;
}
